@import url("https://fonts.googleapis.com/css2?family=Roboto+Condensed:ital,wght@0,100..900;1,100..900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inter+Tight:ital,wght@0,100..900;1,100..900&family=Inter:wght@100..900&family=Roboto+Condensed:ital,wght@0,100..900;1,100..900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Satisfy&display=swap");
:root {
  --blue: rgb(68, 94, 242);
  --primary: #8036e7;
  --lite-gray: #988e9f;
  --web-background: #181325;
  --about-background: #14121c;
  --contact-background: #0e0d10;
  --ff-size-4rem: 4rem;
  --ff-size-2rem: 2rem;
  --ff-size-3rem: 3rem;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  font-size: 62.5%;
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
  width: 100%;
}

/* utility classes */
.color-purple {
  color: var(--primary) !important;
  margin-left: 1rem;
}
.color-white {
  background-color: white;
}
.ff-satcify {
  font-family: "Satisfy", cursive;
}
.ff-Robot {
  font-family: "Roboto Condensed";
}
.ff-inter {
  font-family: "Inter", sans-serif;
}
.flex {
  display: flex !important;
}
.justify-center {
  justify-content: center !important;
}
.align-center {
  align-items: center !important;
}
.m-100vh {
  min-height: 100vh;
}
.flex-row {
  flex-direction: row;
}
.opa-1 {
  opacity: 1 !important;
}
main {
  min-height: 100vh;
  width: 100%;
  position: relative;
}
.navbar-box {
  position: fixed;
  width: 100%;
  height: 5rem;
  justify-content: space-between;
  padding: 3rem 2rem;
  z-index: 99;
}
.heading {
  font-size: 4rem;
  color: white;
  margin-inline: 0.5rem;
}
.lessthan {
  color: var(--blue);
  font-size: 2.5rem;
  font-family: 400;
}
.navbar__options {
  justify-content: space-between;
  gap: 3rem;
  color: white;
  font-size: 2rem;
}
.navbar__option {
  list-style: none;
  height: inherit;
  cursor: pointer;
}
.navbar__option span {
  margin-left: 0.5rem;
  color: var(--blue);
}
.navbar__option:hover {
  color: hsla(0, 0%, 100%, 0.5);
}

.hamburger-btn {
  display: none;
  width: 5rem;
  height: 3rem;
  background-color: var(--web-background);
  border: 0.5px solid #181325;
  border-radius: 2px;
  padding: 0.5rem;
  transition: all 0.3s ease;
  z-index: 1500;
}
.hamburger-btn span {
  display: block;
  width: 2.5rem;
  height: 2px;
  background-color: var(--blue);
  margin: 0 auto;
}
.hamburger-btn span:nth-child(2) {
  margin: 7px auto;
}

.hamburger-btn.active span:first-child {
  transform: rotate(135deg) translate(6px, -6px);
}
.hamburger-btn.active span:nth-child(2) {
  opacity: -1;
}
.hamburger-btn.active span:last-child {
  transform: rotate(-135deg) translate(6px, 6px);
}
.background-main {
  display: none;
  position: fixed;
  width: 2rem;
  height: 2rem;
  background-color: var(--about-background);
  right: 3.5rem;
  top: 2rem;
  border-radius: 50%;
  z-index: 1000;
  transition: transform 0.8s cubic-bezier(0.86, 0, 0.07, 1);
}
.background-main.active {
  transform: scale(200);
}
.navbar__center {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  flex-direction: column;
  gap: 3rem;
  width: 100%;
  z-index: 1003;
}
.navbar__center-option {
  list-style: none;
  color: white;
  font-size: var(--ff-size-3rem);
  transition: all 0.8s cubic-bezier(0.68, -0.55, 0.265, 1.55);
  transform: translateX(-100%);
  text-align: center;
}
.navbar__link {
  text-decoration: none;
  display: inline-block;
  width: 10rem;
  background-image: linear-gradient(
    120deg,
    transparent 0%,
    transparent 50%,
    var(--web-background) 50%
  );
  background-size: 220%;
  color: white;
  transition: all 0.5s;
}
.navbar__link:hover {
  background-position: 100%;
  color: var(--primary);
}
.navbar__center-option.active {
  transition: all 0.8s cubic-bezier(0.68, -0.55, 0.265, 1.55);
  transform: translateX(0%);
}

.Start__section {
  background-image: url(/src/assests/bg-start.webp);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  padding: 7rem 3rem;
  width: 100%;
  height: 100vh;
  min-height: 63rem;
  margin: 0 auto;
  overflow: hidden;
  z-index: 10;
  position: relative;
}
.row {
  font-family: "Roboto Condensed";
  padding-inline: 1rem;
  flex: initial !important;
  align-self: center;
  flex-wrap: wrap;
  width: 100%;
  margin-left: -15px;
  margin-right: -15px;
}
.col-7 {
  padding-left: 15px;
  padding-right: 15px;
  width: 100%;
}
.intro__article {
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
  position: relative;
  min-height: min-content;
  display: block;
}
.timeline {
  height: 300%;
  left: -10px;
  position: absolute;
  top: 11px;
  width: 1px;
  z-index: 9;
  background-color: var(--primary);
}
.ellipse {
  height: 1rem;
  width: 1rem;
  background-color: var(--primary);
  border-radius: 50%;
  transform: translate(-4.5px, -6px);
  position: relative;
  z-index: 9;
}
.ellipse::before {
  content: "";
  position: absolute;
  height: 0.5rem;
  width: 0.5rem;
  border-radius: inherit;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: var(--about-background);
}
.scroll-box {
  position: absolute;
  top: 300px;
  z-index: 3;
  height: 4rem;
  text-decoration: none;
  margin-left: -7px;
  transition: transform 0.3s;
}
.mouse-icon {
  width: 2.5rem;
  height: 2.5rem;
}
.scoll {
  position: absolute;
  font-family: "Roboto Condensed";
  transform: rotate(-90deg) translate(-25px, -18px);
  background-color: #0e0d10b6;
  z-index: 3;
  font-size: 1.6rem;
}
.scoll:active,
.scoll:link {
  color: var(--primary);
}
.scroll-box:hover {
  transform: scale3d(1.1, 1.1, 1);
  margin-left: -7px;
}

.block-reveal {
  align-items: center;
  display: flex;
  display: inline-block;
  overflow: hidden;
  position: relative;
}
.block-reveal--active {
  display: inline-block;
  overflow: hidden;
  position: relative;
}
.start {
  display: inline-block;
  font-family: "Roboto Condensed";
  color: #727272;
  font-size: 1.7rem;
  opacity: 0;
}
.intro__hi {
  display: inline-block;
  font-family: "Roboto Condensed";
  color: white;
  font-size: var(--ff-size-4rem);
  opacity: 0;
  margin-top: 2rem;
}
.intro__friend {
  display: inline-block;
  color: white;
  font-size: var(--ff-size-4rem);
  opacity: 0;
  margin-top: 1rem;
}
.into__letme {
  opacity: 0;
  display: inline-block;
  margin-top: 3rem;
  font-size: var(--ff-size-3rem);
  color: var(--lite-gray);
}

.block-reveal--active .block-reveal__text {
  animation: block-reveal-text 0s;
  animation-delay: 0.4s;
  animation-fill-mode: forwards;
  -o-animation: block-reveal-text 0s;
  -o-animation-delay: 0.4s;
  -o-animation-fill-mode: forwards;
  -moz-animation: block-reveal-text 0s;
  -moz-animation-delay: 0.4s;
  -moz-animation-fill-mode: forwards;
  -webkit-animation: block-reveal-text 0s;
  -webkit-animation-delay: 0.4s;
  -webkit-animation-fill-mode: forwards;
  will-change: transform, opacity;
}
.block-reveal--active .block-reveal__block {
  animation: block-rev-block 0.8s;
  animation-timing-function: cubic-bezier(0.65, 0.05, 0.36, 1);
  -o-animation: block-rev-block 0.8s;
  -o-animation-timing-function: cubic-bezier(0.65, 0.05, 0.36, 1);
  -moz-animation: block-rev-block 0.8s;
  -moz-animation-timing-function: cubic-bezier(0.65, 0.05, 0.36, 1);
  -webkit-animation: block-rev-block 0.8s;
  -webkit-animation-timing-function: cubic-bezier(0.65, 0.05, 0.36, 1);
}
.block-reveal__block {
  background: var(--primary);
  content: "";
  height: 100%;
  position: absolute;
  transform: translateX(calc(100% + 1px));
  width: 100%;
  will-change: transform;
  z-index: 10;
}
/* projesct Section */
.project__section {
  height: 100vh;
  width: 100%;
  background-image: url(/src/assests/bg-project.webp);
  background-size: cover;
}
.about__section {
  height: 100vh;
  width: 100%;
  background-color: var(--about-background);
}
.contact__section {
  height: 100vh;
  width: 100%;
  background-color: var(--contact-background);
}
@keyframes block-rev-block {
  0% {
    transform: translateX(-100%);
  }

  to {
    transform: translateX(calc(100% + 1px));
  }
}
@-webkit-keyframes block-rev-block {
  0% {
    transform: translateX(-100%);
  }

  to {
    transform: translateX(calc(100% + 1px));
  }
}
@-o-keyframes block-rev-block {
  0% {
    transform: translateX(-100%);
  }

  to {
    transform: translateX(calc(100% + 1px));
  }
}
@-o-keyframes block-rev-block {
  0% {
    transform: translateX(-100%);
  }

  to {
    transform: translateX(calc(100% + 1px));
  }
}

@keyframes block-reveal-text {
  0% {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}
@-webkit-keyframes block-reveal-text {
  0% {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}
@-o-keyframes block-reveal-text {
  0% {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}
@-moz-keyframes block-reveal-text {
  0% {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@media (max-width: 1023px) {
  :root {
    --ff-size-4rem: 3.5rem;
    --ff-size-2rem: 1.5rem;
    --ff-size-3rem: 2.5rem;
  }
}
@media (max-width: 860px) {
  :root {
    --ff-size-4rem: 3rem;
    --ff-size-2rem: 1rem;
    --ff-size-3rem: 2rem;
  }
  .navbar__options {
    display: none !important;
  }
  .hamburger-btn {
    display: initial;
  }
  .background-main {
    display: block;
  }
}
@media (max-width: 600px) {
  .block-reveal--active .block-reveal__text {
    animation: block-reveal-text 0s;
    animation-delay: 0.2s; /* Shorter delay for mobile */
    animation-fill-mode: forwards;
  }

  .block-reveal--active .block-reveal__block {
    animation: block-rev-block 0.6s; /* Shorter duration for mobile */
    animation-timing-function: cubic-bezier(0.65, 0.05, 0.36, 1);
  }
}
